<template>
  <div class="register">
    <a-form-model
        ref="registerForm"
        :model="registerForm"
        :rules="rules"
        v-bind="layout"
    >
        <div class="header-title">
            <router-link to="/login">登录</router-link>
            <router-link to="/register" class="active">注册</router-link>
        </div>
        <a-form-model-item has-feedback label="用户名" prop="username">
        <a-input v-model="registerForm.username" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="邮箱" prop="email">
        <a-input v-model="registerForm.email" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="请输入密码" prop="passWord">
        <a-input v-model="registerForm.passWord" type="password" autocomplete="off" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="重复输入密码" prop="checkPass">
        <a-input
            v-model="registerForm.checkPass"
            type="password"
            autocomplete="off"
        />
        </a-form-model-item>
        <a-form-model-item has-feedback label="验证码" prop="code">
        <a-input v-model="registerForm.code" />
        <a-button :loading="loading" @click="getCode" class="btn">
            <span>获取验证码</span>
            <span v-if="loading">({{number}})</span>
        </a-button>
        </a-form-model-item>
        <a-form-model-item has-feedback label="用户类型" prop="role">
        <a-select
            label-in-value
            :default-value="{ key: '普通用户' }"
            style="width: 120px"
            @change="handleChange"
        >
            <a-select-option value="customer">普通用户</a-select-option>
            <a-select-option value="admin">管理员</a-select-option>
        </a-select>
        </a-form-model-item>
        <a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
        <a-button type="primary" @click="submitForm('registerForm')">
            注册
        </a-button>
        <a-button style="margin-left: 10px" @click="resetForm('registerForm')">
            重置
        </a-button>
        </a-form-model-item>
  </a-form-model>
  </div>
</template>
<script>
import { requireCode, register } from '@/api/register';

export default {
  data() {
    const emailReg = /^[\w-]+@[\w.-]+.com$/;
    const registerEmail = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入邮箱'));
      }
      if (emailReg.test(value)) {
        return callback();
      }
      return callback(new Error('邮箱格式不正确！'));
    };
    const checkUsername = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入用户名'));
      }
      return callback();
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.registerForm.checkPass !== '') {
          this.$refs.registerForm.validateField('checkPass');
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.registerForm.passWord) {
        callback(new Error('两次输入的密码不一致'));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        email: '',
        passWord: '',
        code: '',
        username: '',
        role: '',
        checkPass: '',
      },
      timer: null,
      number: 60,
      loading: false,
      rules: {
        passWord: [{ validator: validatePass, trigger: 'change' }],
        checkPass: [{ validator: validatePass2, trigger: 'change' }],
        email: [{ validator: registerEmail, trigger: 'change' }],
        username: [{ validator: checkUsername, trigger: 'change' }],
      },
      layout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 14 },
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const params = {};
            params.email = this.registerForm.email;
            params.password = this.registerForm.passWord;
            params.code = this.registerForm.code;
            params.username = this.registerForm.username;
            params.role = this.registerForm.role;
            await register(params);
            this.$message.info('注册成功');
            this.$router.push('/login');
          } catch (error) {
            window.console.log(error);
            this.$message.error(error);
          }
        }
        return false;
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleChange(value) {
      this.registerForm.role = value.key;
    //   window.console.log(value); // { key: "lucy", label: "Lucy (101)" }
    },
    async getCode() {
      this.loading = true;
      this.timer = setInterval(() => {
        if (this.number === 0) {
          clearInterval(this.timer);
          this.loading = false;
        }
        this.number -= 1;
      }, 1000);
      try {
        const params = {};
        params.email = this.registerForm.email;
        await requireCode(params);
        this.$message.info('邮件发送成功');
      } catch (error) {
        this.$message.error(`${error}`);
      }
    },
  },
};

</script>
<style lang="less">
@import url("~@/assets/css/register.less");
</style>
